import { renderScene } from "./renderScene";

let canvas = null;
let canvasImg = null;
/*eslint-disable*/
self.onmessage = (event) => {
  const {
    elements,
    canvasWorker,
    canvasImgWorker,
    appState,
    scale,
    renderConfig,
    type,
  } = event.data;
  // Ê∏≤ÊüìÈùôÊÄÅÁöÑcanvas
  if (type === "init" || type === "redraw") {
    if (canvasWorker) {
      canvas = canvasWorker;
    }

    renderScene({
      elements,
      appState,
      scale,
      canvas,
      renderConfig,
    });
  }

  // ÂÖ®ÈáèÁîüÊàêÂõæÁâá
  if (type === "init-img" || type === "redraw-img") {
    if (canvasImgWorker) {
      canvasImg = canvasImgWorker;
    }

    renderScene({
      elements,
      appState,
      scale,
      canvasImg,
      renderConfig,
    });
    postMessage({ type: 'img-finish' });
  }
};
